import React from "react"
import { graphgl, Link } from "gatsby"

const AllTagsTemplate = ({ data, pageContext }) => {
  const { tags } = pageContext
  return (
    <div>
      <div> All tags here</div>
      <ul>
        {tags.map((tag, index) => {
          return (
            <li key={index}>
              <Link to={tag}>{tag}</Link>
            </li>
          )
        })}
      </ul>
    </div>
  )
}
export default AllTagsTemplate
